import React, {useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
import { Search,
    Filter,
    MapPin,
    User,
    Phone,
    Star,
    Heart,
    Eye,
    Plus,
    Grid,
    List,
    Shield,
    AlertTriangle,
    Sprout,
    Tractor,
    Users,
    TrendingUp,
    Globe,
    ShoppingCart,
    Package
}
    from 'lucide-react';
import { useCart } from '../contexts/CartContext';
import Footer from "../components/layout/Footer";
import Header from "../components/layout/Header";
import PurchaseModal from "../components/PurchaseModal";
import axios from 'axios';
import { useTranslation } from 'react-i18next';

const Marketplace = () => {
    const [viewMode, setViewMode] = useState('grid');
    const [showFilters, setShowFilters] = useState(false);
    const [selectedCategory, setSelectedCategory] = useState('all');
    const [priceRange, setPriceRange] = useState([0, 1000000]);
    const [searchTerm, setSearchTerm] = useState('');
    const [sortBy, setSortBy] = useState('newest');
    const [selectedProduct, setSelectedProduct] = useState(null);
    const [showPostForm, setShowPostForm] = useState(false);
    const [showPurchaseModal, setShowPurchaseModal] = useState(false);
    const { addToCart } = useCart();
    const { t } = useTranslation();

    const fetchProducts = async () => {
        try {
            const response = await axios.get('http://localhost:5000/api/products', {
                headers: {
                    Authorization: `Bearer ${localStorage.getItem('token')}`
                }
            });
            return response.data; // Return the actual data
        } catch (error) {
            console.error('Error fetching products:', error.response?.data || error.message);
            return []; // Return empty array if there's an error
        }
    };

    const [products, setProducts] = useState([]);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState(null);

    useEffect(() => {
        const loadProducts = async () => {
            try {
                setLoading(true);
                const data = await fetchProducts();
                setProducts(data);
            } catch (err) {
                setError(err.message);
            } finally {
                setLoading(false);
            }
        };
        loadProducts();
    }, []);

    const categories = [
        { id: 'all', name: t('marketplace.categories.all'), icon: '🌾' },
        { id: 'seeds', name: t('marketplace.categories.seeds'), icon: '🌱' },
        { id: 'crops', name: t('marketplace.categories.crops'), icon: '🌽' },
        { id: 'tools', name: t('marketplace.categories.tools'), icon: '🛠️' },
        { id: 'fertilizers', name: t('marketplace.categories.fertilizers'), icon: '🧪' },
        { id: 'equipment', name: t('marketplace.categories.equipment'), icon: '🚜' },
        { id: 'livestock', name: t('marketplace.categories.livestock'), icon: '🐄' },
        { id: 'services', name: t('marketplace.categories.services'), icon: '⚙️' }
    ];

    const filteredProducts = products.filter(product => {
        const matchesCategory = selectedCategory === 'all' || product.category === selectedCategory;
        const matchesSearch = product.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
            product.location.toLowerCase().includes(searchTerm.toLowerCase());
        const matchesPrice = product.price >= priceRange[0] && product.price <= priceRange[1];
        return matchesCategory && matchesSearch && matchesPrice;
    });

    const ProductCard = ({ product }) => (
        <div className="bg-white dark:bg-gray-800 rounded-xl shadow-lg hover:shadow-xl transition-all duration-300 overflow-hidden border border-gray-100 dark:border-gray-700">
            <div className="relative">
                <img
                    src={product.image}
                    alt={product.title}
                    className="w-full h-48 object-cover"
                />
                <button className="absolute top-3 right-3 p-2 bg-white dark:bg-gray-700 rounded-full shadow-md hover:bg-red-50 dark:hover:bg-red-900/20">
                    <Heart className="w-4 h-4 text-gray-600 dark:text-gray-300 hover:text-red-500" />
                </button>
                <div className="absolute bottom-3 left-3">
          <span className={`px-2 py-1 text-xs font-medium rounded-full ${
              product.condition === 'new' ? 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-300' : 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-300'
          }`}>
            {product.condition === 'new' ? t('marketplace.badges.new') : t('marketplace.badges.used')}
          </span>
                </div>
            </div>

            <div className="p-4">
                <h3 className="font-semibold text-gray-900 dark:text-white mb-2 line-clamp-2">{product.title}</h3>
                <div className="flex items-center justify-between mb-2">
                    <span className="text-2xl font-bold text-green-600 dark:text-green-400">Rs: {product.price.toLocaleString()}.00</span>
                    <div className="flex items-center">
                        <Star className="w-4 h-4 text-yellow-400 fill-current" />
                        <span className="text-sm text-gray-600 dark:text-gray-300 ml-1">{product.rating}</span>
                    </div>
                </div>

                <div className="flex items-center text-sm text-gray-600 dark:text-gray-300 mb-2">
                    <MapPin className="w-4 h-4 mr-1" />
                    <span>{product.location}</span>
                </div>

                <div className="flex items-center text-sm text-gray-600 dark:text-gray-300 mb-3">
                    <User className="w-4 h-4 mr-1" />
                    <span>{product.seller}</span>
                </div>

                <div className="flex gap-2">
                    <button
                        onClick={() => setSelectedProduct(product)}
                        className="flex-1 bg-green-600 hover:bg-green-700 text-white py-2 px-4 rounded-lg font-medium transition-colors"
                    >
                        {t('marketplace.viewDetails')}
                    </button>
                    <button className="p-2 border border-gray-300 dark:border-gray-600 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700">
                        <Eye className="w-4 h-4 text-gray-600 dark:text-gray-300" />
                    </button>
                </div>
            </div>
        </div>
    );

    const ProductModal = ({ product, onClose }) => (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center p-4 z-50">
            <div className="bg-white dark:bg-gray-800 rounded-xl max-w-4xl w-full max-h-[90vh] overflow-y-auto">
                <div className="p-6">
                    <div className="flex justify-between items-start mb-4">
                        <h2 className="text-2xl font-bold text-gray-900 dark:text-white">{product.title}</h2>
                        <button
                            onClick={onClose}
                            className="text-gray-500 dark:text-gray-400 hover:text-gray-700 dark:hover:text-gray-200 text-xl font-bold"
                        >
                            ×
                        </button>
                    </div>

                    <div className="grid md:grid-cols-2 gap-6">
                        <div>
                            <img
                                src={product.image}
                                alt={product.title}
                                className="w-full h-64 object-cover rounded-lg"
                            />
                        </div>

                        <div>
                            <div className="mb-4">
                                <span className="text-3xl font-bold text-green-600 dark:text-green-400">Rs: {product.price.toLocaleString()}.00</span>
                                <span className={`ml-3 px-3 py-1 text-sm font-medium rounded-full ${
                                    product.condition === 'new' ? 'bg-green-100 text-green-800 dark:bg-green-900/20 dark:text-green-300' : 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900/20 dark:text-yellow-300'
                                }`}>
                  {product.condition === 'new' ? t('marketplace.badges.new') : t('marketplace.badges.used')}
                </span>
                            </div>

                            <div className="space-y-3 mb-6">
                                <div className="flex items-center">
                                    <MapPin className="w-5 h-5 text-gray-500 dark:text-gray-400 mr-2" />
                                    <span className="text-gray-700 dark:text-gray-300">{product.location}</span>
                                </div>
                                <div className="flex items-center">
                                    <User className="w-5 h-5 text-gray-500 dark:text-gray-400 mr-2" />
                                    <span className="text-gray-700 dark:text-gray-300">{product.seller}</span>
                                    <div className="flex items-center ml-2">
                                        <Star className="w-4 h-4 text-yellow-400 fill-current" />
                                        <span className="text-sm text-gray-600 dark:text-gray-300 ml-1">{product.rating}</span>
                                    </div>
                                </div>
                            </div>

                            <div className="mb-6">
                                <h3 className="font-semibold mb-2">{t('marketplace.description')}</h3>
                                <p className="text-gray-700 dark:text-gray-300">{product.description}</p>
                            </div>

                            <div className="space-y-3">
                                <button
                                    onClick={() => {
                                        addToCart(product._id);
                                        onClose();
                                    }}
                                    className="w-full bg-green-600 hover:bg-green-700 text-white py-3 px-4 rounded-lg font-medium flex items-center justify-center">
                                    <ShoppingCart className="w-4 h-4 mr-2" />
                                    {t('marketplace.addToCart')}
                                </button>
                                <button
                                    onClick={() => {
                                        onClose();
                                        setSelectedProduct(product);
                                        setShowPurchaseModal(true);
                                    }}
                                    className="w-full bg-blue-600 hover:bg-blue-700 text-white py-3 px-4 rounded-lg font-medium flex items-center justify-center">
                                    <Package className="w-4 h-4 mr-2" />
                                    Purchase Now
                                </button>
                                <div className="grid grid-cols-2 gap-2">
                                    <button className="border border-gray-300 hover:bg-gray-50 py-2 px-4 rounded-lg flex items-center justify-center">
                                        <Phone className="w-4 h-4 mr-2" />
                                        {t('marketplace.email')}
                                    </button>
                                    <button className="border border-gray-300 hover:bg-gray-50 py-2 px-4 rounded-lg flex items-center justify-center">
                                        <AlertTriangle className="w-4 h-4 mr-2" />
                                        {t('marketplace.report')}
                                    </button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );

    return (
        <div className="min-h-screen bg-gray-50 dark:bg-gray-900">
            <Header />

            {/* Hero Section */}
            {/*<section className="relative bg-gradient-to-br from-green-50 via-emerald-50 to-teal-50 dark:from-gray-800 dark:via-gray-900 dark:to-gray-800 py-20 lg:py-32">*/}
            {/*    <div className="absolute inset-0 bg-gradient-to-r from-green-600/10 to-emerald-600/10 dark:from-gray-600/20 dark:to-gray-800/20"></div>*/}
            {/*    <div className="relative max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 text-center">*/}
            {/*        <h1 className="text-4xl md:text-6xl font-bold text-gray-900 dark:text-white mb-6 leading-tight">*/}
            {/*            {t('marketplace.hero.title', { defaultValue: 'Farm Marketplace' })}*/}
            {/*        </h1>*/}
            {/*        <p className="text-xl text-gray-600 dark:text-gray-300 mb-8 max-w-3xl mx-auto">*/}
            {/*            {t('marketplace.hero.subtitle', { defaultValue: 'Buy and sell agricultural products, tools, and services directly with local farmers and suppliers.' })}*/}
            {/*        </p>*/}
            {/*    </div>*/}
            {/*</section>*/}

            <div
                className="relative overflow-hidden bg-gradient-to-br from-green-600 via-green-700 to-emerald-800 w-full mt-8">
                {/* Background Pattern */}
                <div className="absolute inset-0 opacity-10 w-full">
                    <div className="absolute inset-0 w-full" style={{
                        backgroundImage: `url("data:image/svg+xml,%3Csvg width='60' height='60' viewBox='0 0 60 60' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='none' fill-rule='evenodd'%3E%3Cg fill='%23ffffff' fill-opacity='0.1'%3E%3Ccircle cx='7' cy='7' r='7'/%3E%3Ccircle cx='53' cy='7' r='7'/%3E%3Ccircle cx='7' cy='53' r='7'/%3E%3Ccircle cx='53' cy='53' r='7'/%3E%3C/g%3E%3C/g%3E%3C/svg%3E")`,
                    }}/>
                </div>

                {/* Floating Elements */}
                <div className="absolute top-10 left-10 animate-float-slow">
                    <div className="bg-white/10 backdrop-blur-sm rounded-full p-3">
                        <Sprout className="h-6 w-6 text-green-200"/>
                    </div>
                </div>
                <div className="absolute top-20 right-20 animate-float-delayed">
                    <div className="bg-white/10 backdrop-blur-sm rounded-full p-3">
                        <Tractor className="h-6 w-6 text-green-200"/>
                    </div>
                </div>
                <div className="absolute bottom-10 left-20 animate-float-slow">
                    <div className="bg-white/10 backdrop-blur-sm rounded-full p-3">
                        <Users className="h-6 w-6 text-green-200"/>
                    </div>
                </div>

                <div className="relative px-4 py-20 sm:px-6 lg:px-8">
                    <div className="max-w-4xl mx-auto text-center">
                        {/* Badge */}
                        <div
                            className="inline-flex items-center bg-white/20 backdrop-blur-sm border border-white/30 rounded-full px-6 py-2 mb-8 animate-fade-in-up">
                            <Globe className="h-4 w-4 text-green-200 mr-2"/>
                            <span className="text-sm font-medium text-green-100">
              {t('marketplace.hero.badge')}
            </span>
                        </div>

                        {/* Main Heading */}
                        <h1 className="text-5xl md:text-6xl lg:text-7xl font-bold text-white mb-6 leading-tight animate-fade-in-up animation-delay-200">
                            {t('marketplace.hero.title1')}
                            <span
                                className="block bg-gradient-to-r from-yellow-300 to-orange-400 bg-clip-text text-transparent pb-2">
              {t('marketplace.hero.title2')}
            </span>
                        </h1>

                        {/* Subtitle */}
                        <p className="text-xl md:text-2xl text-green-100 max-w-3xl mx-auto mb-8 leading-relaxed animate-fade-in-up animation-delay-400">
                            {t('marketplace.hero.subtitle1')}
                            <span className="block mt-2 font-medium text-white">
              {t('marketplace.hero.subtitle2')}
            </span>
                        </p>

                        {/* Feature Pills */}
                        <div
                            className="flex flex-wrap justify-center gap-4 mb-12 animate-fade-in-up animation-delay-600">
                            <div
                                className="flex items-center bg-white/20 backdrop-blur-sm rounded-full px-4 py-2 border border-white/30">
                                <Shield className="h-4 w-4 text-green-200 mr-2"/>
                                <span className="text-sm font-medium text-white">{t('marketplace.pills.secure')}</span>
                            </div>
                            <div
                                className="flex items-center bg-white/20 backdrop-blur-sm rounded-full px-4 py-2 border border-white/30">
                                <TrendingUp className="h-4 w-4 text-green-200 mr-2"/>
                                <span className="text-sm font-medium text-white">{t('marketplace.pills.prices')}</span>
                            </div>
                            <div
                                className="flex items-center bg-white/20 backdrop-blur-sm rounded-full px-4 py-2 border border-white/30">
                                <Users className="h-4 w-4 text-green-200 mr-2"/>
                                <span className="text-sm font-medium text-white">{t('marketplace.pills.local')}</span>
                            </div>
                        </div>

                        {/* CTA Buttons */}
                        <div
                            className="flex flex-col sm:flex-row gap-4 justify-center animate-fade-in-up animation-delay-800">
                            <Link
                                to="/trade"
                                className="group bg-white text-green-700 px-8 py-4 rounded-xl font-bold text-lg hover:bg-green-50 transform hover:scale-105 transition-all duration-300 shadow-lg hover:shadow-xl">
              <span className="flex items-center justify-center">
                {t('marketplace.cta.startTrading')}
                <TrendingUp className="ml-2 h-5 w-5 group-hover:translate-x-1 transition-transform duration-300"/>
              </span>
                            </Link>
                            <Link to="/learnmore"
                                  className="group bg-transparent text-white border-2 border-white/30 backdrop-blur-sm px-8 py-4 rounded-xl font-bold text-lg hover:bg-white/10 hover:border-white/50 transform hover:scale-105 transition-all duration-300">
              <span className="flex items-center justify-center">
                {t('marketplace.cta.learnMore')}
                <Sprout className="ml-2 h-5 w-5 group-hover:rotate-12 transition-transform duration-300"/>
              </span>
                            </Link>
                        </div>
                    </div>
                </div>

                <style jsx>{`
                    @keyframes fadeInUp {
                        from {
                            opacity: 0;
                            transform: translateY(30px);
                        }
                        to {
                            opacity: 1;
                            transform: translateY(0);
                        }
                    }

                    @keyframes float {
                        0%, 100% {
                            transform: translateY(0px);
                        }
                        50% {
                            transform: translateY(-20px);
                        }
                    }

                `}</style>
            </div>

            <div className="bg-white shadow-sm border-b">
                <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6 pt-24 w-full">
                    {/* Search and Filters */}
                    <div className="flex flex-col lg:flex-row gap-4 items-center">
                        <div className="relative flex-1 max-w-2xl mx-auto lg:mx-0">
                            <Search
                                className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-5 h-5"/>
                            <input
                                type="text"
                                placeholder={t('marketplace.searchPlaceholder')}
                                value={searchTerm}
                                onChange={(e) => setSearchTerm(e.target.value)}
                                className="w-full pl-10 pr-4 py-3 border border-gray-300 rounded-xl focus:ring-2 focus:ring-green-500 focus:border-transparent"
                            />
                        </div>

                        <div className="flex gap-2">
                            <button
                                onClick={() => setShowFilters(!showFilters)}
                                className="flex items-center px-4 py-3 border border-gray-300 rounded-xl hover:bg-gray-50"
                            >
                                <Filter className="w-5 h-5 mr-2"/>
                                {t('marketplace.filters')}
                            </button>

                            <button
                                onClick={() => setShowPostForm(true)}
                                className="flex items-center px-6 py-3 bg-green-600 text-white rounded-xl hover:bg-green-700"
                            >
                                <Plus className="w-5 h-5 mr-2"/>
                                {t('marketplace.postAd')}
                            </button>
                        </div>
                    </div>
                </div>
            </div>

            <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
                <div className="flex flex-col lg:flex-row gap-8">
                    {/* Sidebar */}
                    <div className="lg:w-64 space-y-6">
                        {/* Categories */}
                        <div className="bg-white rounded-xl shadow-sm p-6">
                            <h3 className="font-semibold text-gray-900 mb-4">{t('marketplace.categories.title')}</h3>
                            <div className="space-y-2">
                                {categories.map(category => (
                                    <button
                                        key={category.id}
                                        onClick={() => setSelectedCategory(category.id)}
                                        className={`w-full text-left px-3 py-2 rounded-lg transition-colors ${
                                            selectedCategory === category.id
                                                ? 'bg-green-100 text-green-800'
                                                : 'hover:bg-gray-100'
                                        }`}
                                    >
                                        <span className="mr-2">{category.icon}</span>
                                        {category.name}
                                    </button>
                                ))}
                            </div>
                        </div>

                        {/* Price Range */}
                        <div className="bg-white rounded-xl shadow-sm p-6">
                            <h3 className="font-semibold text-gray-900 mb-4">{t('marketplace.priceRange')}</h3>
                            <div className="space-y-4">
                                <div className="flex gap-2">
                                    <input
                                        type="number"
                                        placeholder={t('marketplace.min')}
                                        value={priceRange[0]}
                                        onChange={(e) => setPriceRange([parseInt(e.target.value) || 0, priceRange[1]])}
                                        className="w-24 px-2 py-2 border border-gray-300 rounded-lg"
                                    />
                                    <input
                                        type="number"
                                        placeholder={t('marketplace.max')}
                                        value={priceRange[1]}
                                        onChange={(e) => setPriceRange([priceRange[0], parseInt(e.target.value) || 1000000])}
                                        className="w-24 px-2 py-2 border border-gray-300 rounded-lg"
                                    />
                                </div>
                            </div>
                        </div>

                        {/* Safety Tips */}
                        <div className="bg-yellow-50 rounded-xl p-6 border border-yellow-200">
                            <div className="flex items-center mb-3">
                                <Shield className="w-5 h-5 text-yellow-600 mr-2"/>
                                <h3 className="font-semibold text-yellow-800">{t('marketplace.safetyTips.title')}</h3>
                            </div>
                            <ul className="text-sm text-yellow-700 space-y-2">
                                <li>• {t('marketplace.safetyTips.meetPublic')}</li>
                                <li>• {t('marketplace.safetyTips.noAdvance')}</li>
                                <li>• {t('marketplace.safetyTips.verifySeller')}</li>
                                <li>• {t('marketplace.safetyTips.inspectItems')}</li>
                            </ul>
                        </div>
                    </div>

                    {/* Main Content */}
                    <div className="flex-1">
                        {/* Toolbar */}
                        <div className="flex justify-between items-center mb-6">
                            <div className="flex items-center gap-4">
                <span className="text-gray-600">
                  {t('marketplace.productsFound', { count: filteredProducts.length })}
                </span>
                                <select
                                    value={sortBy}
                                    onChange={(e) => setSortBy(e.target.value)}
                                    className="px-3 py-2 border border-gray-300 rounded-lg"
                                >
                                    <option value="newest">{t('marketplace.sort.newest')}</option>
                                    <option value="price-low">{t('marketplace.sort.priceLow')}</option>
                                    <option value="price-high">{t('marketplace.sort.priceHigh')}</option>
                                    <option value="location">{t('marketplace.sort.location')}</option>
                                </select>
                            </div>

                            <div className="flex items-center gap-2">
                                <button
                                    onClick={() => setViewMode('grid')}
                                    className={`p-2 rounded-lg ${viewMode === 'grid' ? 'bg-green-100 text-green-600' : 'text-gray-400'}`}
                                >
                                    <Grid className="w-5 h-5"/>
                                </button>
                                <button
                                    onClick={() => setViewMode('list')}
                                    className={`p-2 rounded-lg ${viewMode === 'list' ? 'bg-green-100 text-green-600' : 'text-gray-400'}`}
                                >
                                    <List className="w-5 h-5"/>
                                </button>
                            </div>
                        </div>

                        {/* Products Grid */}
                        <div className={`grid gap-6 ${
                            viewMode === 'grid'
                                ? 'grid-cols-1 md:grid-cols-2 xl:grid-cols-3'
                                : 'grid-cols-1'
                        }`}>
                            {filteredProducts.map(product => (
                                <ProductCard key={product._id} product={product}/>
                            ))}
                        </div>

                        {filteredProducts.length === 0 && (
                            <div className="text-center py-12">
                                <div className="text-gray-400 text-6xl mb-4">📦</div>
                                <h3 className="text-xl font-semibold text-gray-600 mb-2">{t('marketplace.noProducts')}</h3>
                                <p className="text-gray-500">{t('marketplace.tryAdjust')}</p>
                            </div>
                        )}
                    </div>
                </div>
            </div>

            {/* Product Modal */}
            {selectedProduct && (
                <ProductModal
                    product={selectedProduct}
                    onClose={() => setSelectedProduct(null)}
                />
            )}

            {/* Purchase Modal */}
            <PurchaseModal
                show={showPurchaseModal}
                onHide={() => setShowPurchaseModal(false)}
                product={selectedProduct}
                onSuccess={(order) => {
                    console.log('Order created:', order);
                    // You can add navigation to orders page here
                }}
            />

            <Footer/>
        </div>
    );
};

export default Marketplace;
